@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html,
body {
  margin: 0;
  width: 100%;
}

h1,
h2,
p,
i,
a,
.first-letter,
.authorName a {
  color: rgba(0, 0, 0, 0.84);
  text-rendering: optimizeLegibility;
}

h1 {
  font-family: 'Dancing Script', cursive;
  font-size: 48px;
  text-align: left;
  margin-bottom: 8px;
}

h2 {
  font-family: 'Dancing Script', cursive;
  font-size: 30px;
  font-weight: 700;
  padding: 0;
  margin: 40px 0 40px 0px;
  text-align: left;
  line-height: 34.5px;
  letter-spacing: -0.45px;
}

p,
i,
a,
li {
  margin-top: 21px;
  font-family: 'Roboto Mono', monospace;
  font-size: 21px;
  letter-spacing: -0.03px;
  line-height: 1.58;
}

a {
  text-decoration: underline;
}

blockquote {
  font-family: "Playfair Display", serif;
  font-size: 25px;
  font-style: italic;
  letter-spacing: -0.36px;
  line-height: 44.4px;
  overflow-wrap: break-word;
  margin: 30px 0 33px 0;
  /* text-align: center; */
  color: rgba(0, 0, 0, 0.68);
  padding: 0 0 0 50px;
}

code {
  font-size: 18px;
  background: rgba(0, 0, 0, 0.05);
  border-radius: 2px;
  padding: 3px 5px;
}

pre > code {
  background: transparent !important;
  font-family: "Quicksand";
  font-size: 18px;
  padding: 0px;
}

pre {
  margin-top: 30px;
  border-radius: 10px;
  padding: 20px !important;
}

mark,
.highlighted {
  background: #7dffb3;
}

.first-letter {
  overflow-wrap: break-word;
  font-family: "Playfair Display", serif;
  font-size: 60px;
  line-height: 60px;
  display: block;
  position: relative;
  float: left;
  margin: 0px 7px 0 -5px;
}

.subtitle {
  font-family: 'Dancing Script', cursive;
  color: rgba(0, 0, 0, 0.54);
  margin: 0 0 24px 0;
}

::-moz-selection {
  background-color: lavender;
}

::selection {
  background-color: lavender;
}

.blog-div-main {
  margin: 2rem auto 0;
  width: 90%;
  max-width: 700px;
  padding: 20px 10px;
}

/* Slide up */

.blog-post-anchor {
  color: #343139;
  text-decoration: none;
  /* border-bottom: 0.125em solid #2b2827; */
  box-shadow: inset 0 -0.125em 0 #0e0d0d;
  -webkit-transition: box-shadow 270ms cubic-bezier(0.77, 0, 0.175, 1), color 270ms cubic-bezier(0.77, 0, 0.175, 1);
  transition: box-shadow 270ms cubic-bezier(0.77, 0, 0.175, 1), color 270ms cubic-bezier(0.77, 0, 0.175, 1);
  padding-right: 2px;
  padding-left: 2px;
}

.blog-post-anchor:hover {
  box-shadow: inset 0 -1.125em 0 #080808;
  color: white;
}

/* a:focus {
  background: #fff;
  outline: none;
  background: #080808;
  color: #fff;
  box-shadow: 8px 8px 24px rgba(0, 0, 0, 0.2);
} */

.blog-title {
  /* text-align: center; */
}

img {
  max-width: 100%;
  height: auto;
}

.blog-date {
  color: rgba(0, 0, 0, 0.54);
  font-weight: 400;
  font-size: 14px;
  margin-bottom: 0px;
  margin-top: 0px;
}
.blog-date > a {
  font-size: 14px;
  text-decoration: none;
  -webkit-transition-duration: 0.15s, 0.15s, 0.15s;
          transition-duration: 0.15s, 0.15s, 0.15s;
  -webkit-transition-timing-function: ease-in, ease-in, ease-in;
          transition-timing-function: ease-in, ease-in, ease-in;
  -webkit-transition-delay: 0s, 0s, 0s;
          transition-delay: 0s, 0s, 0s;
  color: rgba(0, 0, 0, 0.54);
}
.blog-date > a:hover {
  color: rgba(0, 0, 0, 0.84);
}
.avatar {
  width: 50px;
  height: 50px;
  border-radius: 40px;
  margin: 10px;
  margin-left: 0px;
}

.author-name {
  margin-bottom: 0px;
  margin-top: 0px;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.84);
}

.author-details {
  display: flex;
  align-items: center;
}
.blog-view {
  margin: 2rem auto 0;
  width: 90%;
  max-width: 700px;
  padding: 20px 10px;
  position: relative;
}
.reaction-github-emoji {
  position: absolute;
  bottom: 60px;
}

.anim-scale-in {
  -webkit-animation-name: scale-in;
          animation-name: scale-in;
  -webkit-animation-duration: 0.15s;
          animation-duration: 0.15s;
  -webkit-animation-timing-function: cubic-bezier(0.2, 0, 0.13, 1.5);
          animation-timing-function: cubic-bezier(0.2, 0, 0.13, 1.5);
}

@-webkit-keyframes scale-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scale-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media only screen and (max-width: 768px) {
  .blog-title {
    /* text-align: center; */
    font-size: 30px;
  }

  p,
  i,
  a,
  li {
    font-size: 18px;
  }
  blockquote {
    font-size: 20px;
  }
}

.reaction-text-github-div {
  padding-top: 5px;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 12px;
  display: inline-block;
}

.reaction-text-github-div > p {
  font-size: 14px;
  line-height: 1.5;
  color: rgb(118, 118, 118);
  margin: 6px 12px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica;
}
.reaction-text-github-div > div {
  height: 1px;
  margin: 8px 1px 0px;
  background-color: rgb(229, 229, 229);
}
.reaction-text-github-div > span > a{
    display: block;
    text-align: center;
    margin: 0;
    padding: 5px;
    line-height: 1.5;
    font-size: 14px;
    text-decoration: none;
    -webkit-transition-duration: 0.15s, 0.15s, 0.15s;
            transition-duration: 0.15s, 0.15s, 0.15s;
    -webkit-transition-timing-function: ease-in, ease-in, ease-in;
            transition-timing-function: ease-in, ease-in, ease-in;
    -webkit-transition-delay: 0s, 0s, 0s;
            transition-delay: 0s, 0s, 0s;
    color: rgba(0, 0, 0, 0.54);
}

.reaction-text-github-div > span > a:hover {
  color: black;
}
.main-header {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  padding: 5em;
  text-align: center;
  background-color: #f1f6f8;
}

.wrap-big {
  margin: 0 auto;
  padding: 0 1em;
  max-width: 1140px;
  text-align: center;
}

.header-title {
  font-weight: 700;
  line-height: 1.15;
  margin: 1.25rem 0;
  font-size: 4.5em;
  text-align: center;
  color: #ff5252;
}
.header-subtitle {
  font-weight: 400;
  color: #37474f;
  line-height: 1.15;
  margin: 1.25rem 0;
  font-size: 2.5em;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .header-subtitle {
    font-size: 2em;
  }

  .header-title {
    font-size: 3em;
  }

  .main-header {
    padding: 3em;
  }
}

.blog-card-div{
    cursor: pointer;
    margin-bottom: 6rem;
}

.blog-card-description > p {
    font-size: 18px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}

.blog-card-title > h2{
    font-size: 34px;
    -webkit-transition-duration: 0.15s, 0.15s, 0.15s;
            transition-duration: 0.15s, 0.15s, 0.15s;
    -webkit-transition-timing-function: ease-in, ease-in, ease-in;
            transition-timing-function: ease-in, ease-in, ease-in;
    -webkit-transition-delay: 0s, 0s, 0s;
            transition-delay: 0s, 0s, 0s;
   
}

.blog-card-title > h2:hover{
    opacity: 0.7
   
}


.blog-catgeory{
    display: inline-block;
    background: #009bbb;
    border-radius: 3px;
    padding: 3px 15px;
    font-size: 12px;
    text-transform: uppercase;
    color: #fff;
    font-weight: 300;
    line-height: 28px;
    font-family: "Quicksand";
    margin-right: 10px;
}

.reading-time{
    color: #b5b5b5;
    font-size: 15px
}

.blog-card-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
